body {
    margin: 0;
    font-family: 'Poppins', Arial, sans-serif;
    background: #f4f4f9;
    color: #333;
}

/* From Uiverse.io by joe-watson-sbf */ 
.btn {
    font-size: 17px;
    background: transparent;
    border: none;
    padding: 1em 1.5em;
    color: #ffedd3;
    text-transform: uppercase;
    position: absolute;
    top: 5px;
    right: 20px;
    transition: 0.5s ease;
    cursor: pointer;
  }
  
  .btn::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 0;
    background-color: #ffc506;
    transition: 0.5s ease;
  }
  
  .btn:hover {
    color: #1e1e2b;
    transition-delay: 0.5s;
  }
  
  .btn:hover::before {
    width: 100%;
  }
  
  .btn::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 0;
    width: 100%;
    background-color: #ffc506;
    transition: 0.4s ease;
    z-index: -1;
  }
  
  .btn:hover::after {
    height: 100%;
    transition-delay: 0.4s;
    color: aliceblue;
  }

  
.header {
    background: linear-gradient(159deg, #2465c1 10%, #2d91a7 100%);
    color: #fff;
    padding: 20px 10px;
    text-align: center;
    position: sticky;
    top: 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    z-index: 1;
}

.header h1 {
    font-size: 24px;
}

.brand-highlight {
    color: #ffdd00;
}

.hero {
    text-align: center;
    background: linear-gradient(to right, #ffdf0e, #ffe493);
    padding:10px;
    margin-bottom: 20px;
}

.hero h2 {
    font-size: 24px;
    color: #b62020;
}

.hero .tagline {
    margin-top: 4px;
    font-size: 16px;
    color: #555;
}
.match{
    max-width: 700px;
    margin: auto;
    padding: 0 6px;
}
.match_card {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    padding-top: 15px;

}

.matches {
    position: relative;
    background: linear-gradient(26deg, #ffcd2a72, #fff1f1c3,#ffeded);
    box-shadow: 5px 0px 15px rgba(0, 0, 0, 0.209);
    margin: auto;
    margin-top: 10px;
    border-radius: 12px 12px;
    overflow: hidden;
    
    
}

.league {
    display: flex;
    flex-direction: row;
    border-radius: 12px 12px 0 0;

    border-bottom: 1px solid #85858521;
    font-size: 12px;
    color: #505050;
    padding: 5px 0px;
    padding-left: 10px;
    background: linear-gradient(24deg, #fff1f1, #fff1f1c3,#ffeded);

}
.league .timing{
    color: #444444;

    flex-grow: 1;
    text-align: right;
    margin-right: 10px;
}
.match_card .team .team_info {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 14px;
}

.match_card .team {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.match_card .team_b{
    align-items: end;
}
.match_card .team .img_con {
    width: 70px;
    height: 70px;
    overflow: hidden;
    border-radius: 100px;
}

.match_card .team .img_con img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.match_status{
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.match_status .live_data {
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: center;
    justify-content: center;
}

.match_status .live_data p {
    text-wrap: nowrap;
    font-size: 13px;
    color: #6e6e6e;
    margin-bottom: 2px;
}

.match_status .live_data h3 {
    padding: 5px 8px;
    border-radius: 18px;
    font-size: 11px;
    color: #b62020;
    background-color: rgba(255, 24, 24, 0.119);

}

.team_b_name{
    text-align: right;
}
.team_b_name,
.team_a_name {
    font-size: 14px;
    color: #6e6e6e;

}

.match_card .team_b_nick,
.match_card .team_a_nick {
    color: black;
    font-size: 15px;
    font-weight: bold;
}

.view_card {
    padding:8px 0px;
    margin-left: -6px;
    border-top: 1px solid #6e6e6e46;
    display: flex;
    width: 105%;
    
}

.view_card .match_lean {
    margin-left: 12px;
    color: #656565;
    font-size: 12px;
    background: #fffcd6ef;
    padding: 2px 8px;
    border-radius: 6px;

}
.view_card .match_lean span{

    font-weight: bolder;
}
.upc_matches{
margin-bottom: 15px;
font-weight: bold;
}
.no_matches_found{
    text-align: center;
    padding: 20px;
    font-size:20px;
    font-weight: bold;
}

.my_matches{
    margin-bottom: 20px;
}
@media (max-width:800px) {
    .header {
        padding:15px;
        font-size: 30px;
    }
    .hero h2 {
        
        font-size: 20px;
    }
    .match_card .team  .img_con{
        width:60px;
        height: 60px;
    }
}


@media (max-width:500px) {
    .header {
        font-size: 25px;
    }
    .hero h2 {
        
        font-size: 20px;
    }


    .match_card .team  .img_con{
        width:55px;
        height: 55px;
    }
   
.match_card .team_b_nick,
.match_card .team_a_nick{
        font-size: 14px;

    }
    .team_b_name,
.team_a_name {
    font-size: 13px;
}
}

@media (max-width:450px) {
    .match_card .team  .img_con{
        width:50px;
        height: 50px;
    }
   
.match_card .team_b_nick,
.match_card .team_a_nick{
        font-size: 13px;

    }
    .team_b_name,
.team_a_name {
    font-size: 12.5px;
}
}

